<template>
	<view class="diy_notice">
		<view class="row">
			<view class="col-1">
				<uni-icons type="sound" size="20"></uni-icons>
			</view>
			<view class="col-11">
				<swiper class="notice" :indicator-dots="indicatorDots" :autoplay="autoplay" :interval="interval" :duration="duration"
				 :vertical="true">
					<swiper-item v-for="(o, i) in list" :key="i">
						<view class="swiper-item">
							<text>{{ o[vm.title] }}</text>
						</view>
					</swiper-item>
				</swiper>
			</view>
		</view>

	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array,
				default: function() {
					return [];
				}
			},
			vm: {
				type: Object,
				default: function() {
					return {
						img: "img",
						title: "title",
					}
				}
			}
		},
		data() {
			return {
				background: ['color1', 'color2', 'color3'],
				indicatorDots: true,
				autoplay: true,
				interval: 2000,
				duration: 500
			}
		},
		methods: {
			changeIndicatorDots(e) {
				this.indicatorDots = !this.indicatorDots
			},
			changeAutoplay(e) {
				this.autoplay = !this.autoplay
			},
			intervalChange(e) {
				this.interval = e.target.value
			},
			durationChange(e) {
				this.duration = e.target.value
			}
		}
	}
</script>

<style scoped>
	.diy_notice {
		padding: 0.5rem .75rem;
		background-color: #f3f3f3;
	}

	.notice {
		height: 1.5rem;
	}
</style>
